.row {
    margin-top: 3.8%;
    display: flex;
    justify-content: space-around;
    width: 96%;
    height: 20%;
    td {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 20%;
        height: 100%;
    }
    .cell {
        border-radius: 8px;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 1;
    }
}