.GameContainer {
    margin-top: 7vh;
    width: 100%;
    height: 30vw;
    background-color: #bbada0;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    table {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}